// Stars
function createStar() {
    const geometry = new THREE.SphereGeometry(0.4, 16, 16);
    const material = new THREE.MeshBasicMaterial({ color: 0xffff });
    const star = new THREE.Mesh(geometry, material);
    
    // Random position for stars
    star.position.set(
        (Math.random() - 0.6) * 350,
        (Math.random() - 0.4) * 10,
        (Math.random() - 0.5) * 350
    );

    scene.add(star);
}

// Create multiple stars
for (let i = 0; i < 60; i++) {
    createStar();
}


